.modal {
  display: none; }
  .modal.modal-open {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 3;
    background-color: #fff;
    overflow: auto; }
  @media (min-width: 1024px) {
    .modal.modal-on-mobile-only {
      background-color: transparent;
      height: auto;
      display: block;
      position: initial;
      width: auto; }
      .modal.modal-on-mobile-only .modal-inner {
        position: initial;
        padding: 0;
        width: auto;
        height: auto;
        top: inherit;
        left: inherit; }
      .modal.modal-on-mobile-only .modal-close {
        display: none; } }

.modal-inner {
  width: 100%;
  height: 100%;
  padding-top: 1.5rem; }
  @media (min-width: 768px) {
    .modal-inner {
      padding-top: 3rem; } }
  .modal-inner.container {
    max-width: 64rem; }

.modal-close {
  position: fixed;
  top: 1rem;
  right: 1rem;
  padding: 0.25rem;
  background-color: #cfcfcf;
  text-align: center;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  z-index: 4; }
  .modal-close:hover {
    background-color: #c1c1c1; }



